<template>
  <div class="working" id="app">
    <img src="../assets/photo_2018-12-30_15-52-24.jpg" alt="">

    <div class="working__info">
      <p>You have reached an unknown destination..</p>

        <span>Let's go
        <router-link class="fade-link" to="/">back home</router-link>
      </span>
    </div>

  </div>
</template>

<script>
    export default {
        name: "InProgress"
    }
</script>

<style lang="sass" scoped>
@import '../assets/sass/imports'

.working
  height: 100%

  display: flex
  flex-flow: wrap column
  align-items: center
  justify-content: center

  font-size: 1.5em
  text-align: center

  p
    margin-bottom: 1em

  span
    display: block

    font-size: $f-h4

  .fade-link
    color: $c-accent

    position: relative

    &::before
      content: ''
      z-index: -1
      left: 0
      bottom: -4px
      width: 0
      height: 2px
      position: absolute

      border-radius: 2em
      background-color: currentColor

      transition: width 330ms ease-in-out

    &:hover::before,
    &:focus::before
      width: 100%


</style>



