<template>
  <div class="c-registration" id="app">
    <img class="registration__background" src="../assets/buildings-bg.png" alt="">

    <form class="o-form"
          name="form"
          action=""
          @submit.prevent="checkForm"
          novalidate="true">

      <div class="o-form__field">
        <div class="o-form__headline">
          <p>ФИО</p><span>*</span>
        </div>

        <div class="o-form__field">
          <input type="text"
                 v-model="form.name"
                 :class="{ 'input--invalid' : !form.name }"
                 required>
          <label></label>
        </div>
      </div>

      <div class="o-form__field">
        <div class="o-form__headline">
          <p>Почта</p>
        </div>

        <div class="o-form__field">
          <input type="email"
                 v-model="form.email"
                 @focus="validEmail"
                 :class="{ 'input--invalid' : !form.email }">
          <label></label>
        </div>
      </div>

      <div class="o-form__field">
        <div class="o-form__headline">
          <p>Телефон</p><span>*</span>
        </div>

        <div class="o-form__field">
          <input @keypress="isNumber()"
                 type="tel"
                 name="phone"
                 v-model="form.phone"
                 maxlength="10"
                 :class="{ 'input--invalid' : !form.phone || !validNumber }"
                 required>
          <label></label>
        </div>
      </div>

      <div class="o-form__field">
        <div class="o-form__headline">
          <p>Пароль</p><span>*</span>
        </div>

        <div class="o-form__field">
          <input type="password"
                 v-model="form.password"
                 :class="{ 'input--invalid' : !form.password }"
                 required>
          <label></label>
        </div>
      </div>

      <div class="o-form__field">
        <div class="o-form__headline">
          <p>Подтверждение</p><span>*</span>
        </div>

        <div class="o-form__field">
          <input type="password"
                 v-model="form.passwordConf"
                 :class="{ 'input--invalid' : form.passwordConf !== form.password }"
                 required>
          <label></label>
        </div>
      </div>

      <button class="o-button">
        <span>
          Продолжить
        </span>
        <i class="material-icons teal button__icon">send</i>
      </button>
    </form>
  </div>
</template>

<script>
  export default {
    name: 'Registration',
    data() {
      return {
        errors: [],
        form: {
          name: null,
          email: null,
          phone: null,
          password: null,
          passwordConf: null
        }
      }
    },
    methods: {
      checkForm: function () {
        this.errors = [];


        if (!this.form.name) {
          this.errors.push("Name required.");
        }
        if (!this.form.email) {
          this.errors.push('Email required.');
        } else if (!this.validEmail(this.form.email)) {
          this.errors.push('Valid email required.');
        }

        if (!this.form.phone) {
          this.errors.push('Phone required.');
        } else if (!this.validNumber(this.form.phone)) {
          this.errors.push('Valid phone required');
        }


        if (!this.form.password) {
          this.errors.push('Password is required')
        }
        if (this.form.password !== this.form.passwordConf) {
          this.errors.push('Passwords doesn\'t match')
        }

        console.log(this.errors)
        console.log(this.form)

        if (!this.errors.length) {
          // return true

          this.$router.push('/FormDetails')
        }

      },
      validEmail: function (email) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
      },
      isNumber: function(evt) {
        evt = (evt) ? evt : window.event;
        var charCode = (evt.which) ? evt.which : evt.keyCode;

        if ((charCode > 31 && (charCode < 48 || charCode > 57 && charCode !== 46)) ) {
          evt.preventDefault();
        } else {
          return true
        }
      },
      validNumber: function (number) {
        if (number.length < 10) {
          return false
        } else {
          const re = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im;
          return re.test(number)
        }
      }
    }
  }
</script>

<style lang="sass" scoped>

.c-registration
  height: 100%

  display: flex
  align-items: center
  justify-content: center

  .registration__background
    z-index: -1
    left: 0
    bottom: 0
    position: absolute
    transform: scale(.6)
    transform-origin: bottom left
</style>



