<template>
  <div id="app">
    <header class="header line line--header">
      <HeaderComponent></HeaderComponent>

      <!--<img class="header__icon" src="assets/login.svg" alt="">-->
      <span style="cursor: pointer">
        <svg class="header__icon" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" id="Capa_1" x="0px" y="0px" viewBox="0 0 56 56" style="enable-background:new 0 0 56 56;" xml:space="preserve">
        <path d="M25.293,39.293c-0.391,0.391-0.391,1.023,0,1.414C25.488,40.902,25.744,41,26,41s0.512-0.098,0.707-0.293l11.999-11.999   c0.093-0.092,0.166-0.203,0.217-0.326c0.101-0.244,0.101-0.519,0-0.764c-0.051-0.123-0.125-0.234-0.217-0.327L26.707,15.293   c-0.391-0.391-1.023-0.391-1.414,0s-0.391,1.023,0,1.414L35.586,27H5c-0.553,0-1,0.447-1,1s0.447,1,1,1h30.586L25.293,39.293z"/>
        <path d="M51,0H14c-0.553,0-1,0.447-1,1v21c0,0.553,0.447,1,1,1s1-0.447,1-1V2h35v52H15V34c0-0.553-0.447-1-1-1s-1,0.447-1,1v21   c0,0.553,0.447,1,1,1h37c0.553,0,1-0.447,1-1V1C52,0.447,51.553,0,51,0z"/>
      </svg>
      </span>

    </header>

    <main class="main">
      <div class="line line--left"></div>
      <div class="line line--right"></div>

      <div class="content">
        <transition name="fade">
          <router-view></router-view>
        </transition>
      </div>
    </main>

    <footer class="footer line line--footer">
      <div class="footer__items">
        <div class="nav">
          <router-link to="/InProgress">О нас</router-link>
          <router-link to="/InProgress">Услуги</router-link>
          <router-link to="/InProgress">Туториал</router-link>
          <router-link to="/InProgress">Контакты</router-link>
          <router-link to="/InProgress">Саппорт</router-link>
          <router-link to="/">Регистрация</router-link>
        </div>
      </div>

      <div class="social"></div>
    </footer>
  </div>
</template>

<script>
import HeaderComponent from './components/Header-component'

  export default {
    name: 'App',
    components: {
      HeaderComponent
    }
  }
</script>






<style lang="sass">
@import 'assets/sass/imports'



//=============================================
              		  FONTS
//=============================================




@font-face
  font-family: 'Gotham Pro'
  src: url('assets/fonts/GothaProReg.eot')
  src: local('☺'), url('assets/fonts/GothaProReg.woff') format('woff'), url('assets/fonts/GothaProReg.ttf') format('truetype'), url('assets/fonts/GothaProReg.svg') format('svg')
  font-weight: normal
  font-style: normal

@font-face
  font-family: 'Gotham Pro'
  src: url('assets/fonts/GothaProLig.eot')
  src: local('☺'), url('assets/fonts/GothaProLig.woff') format('woff'), url('assets/fonts/GothaProLig.ttf') format('truetype'), url('assets/fonts/GothaProLig.svg') format('svg')
  font-weight: normal
  font-style: normal





//=============================================
                BORDER LINES
//=============================================



.line
  background-color: $c-line-gray
  position: fixed

.line--header, .line--footer
  z-index: 1

.header__items, .footer__items
  display: flex

.line--header
  top: 0
  left: 0
  width: 100%
  height: $l-border-width
  padding: 0 $l-border-width

  align-items: center
  /*justify-content: space-around*/
  p
    width: 25%
    display: flex
    justify-content: center

.header, .footer
  display: flex
  align-items: center






//=============================================
              		  HEADER
//=============================================



.header
  position: relative

  .header__items
    width: 100%
    height: 100%
    position: relative

    display: flex
    align-items: center
    justify-content: space-around

    font-size: $f-h4
    font-weight: 400
    color: $c-primary

    p.is-current
      font-weight: 600

    &::after, &::before
      content: ''
      left: 0
      right: 0
      bottom: 0
      height: 4px
      display: block
      position: absolute

    &::after
      width: 25%
      background-color: $c-accent

    &::before
      width: 100%
      background-color: $c-light-gray

  .header__icon
    /*10vh - line width*/
    top: calc(10vh / 2 - 18px)
    right: calc(10vh / 2 - 19px)
    position: absolute

    width: 36px
    height: 38px
    fill: $c-accent





//=============================================
              		  MAIN
//=============================================



.main

  .content
    /*20vh - width of 2 border lines*/
    width: calc(100vw - 20vh)
    height: 80vh
    margin: 0 $l-border-width
    position: relative

  .line--left,
  .line--right
    top: 0
    bottom: 0
    width: $l-border-width
    height: 100%

  .line--left
    left: 0
  .line--right
    right:  0





//=============================================
              		  FOOTER
//=============================================



.footer

  .footer__items
    align-items: center
    /*10vh - line width*/
    padding: 0 calc(10vh + 1.094em)


    .nav
      will-change: opacity
      transition: opacity 330ms ease

      a
        margin: 0 1.125em
        will-change: opacity
        transition: opacity 330ms ease
      &:hover a:not(:hover)
        opacity: .4

.line--footer
  right: 0
  left: 0
  bottom: 0
  width: 100%
  height: $l-border-width



.fade-enter,
.fade-leave-to
  opacity: 0

  .fade-link
    opacity: 0
    /*transition: opacity 330ms 1s ease*/

.fade-enter-to,
.fade-leave
  opacity: 1

  .fade-link
    opacity: 1
    /*transition: opacity 330ms 1s ease*/

.fade-enter-active,
.face-leave-active
  transition: opacity 330ms ease-in


::selection
  color: $c-accent

</style>



